import React from 'react'
import './MobileMenu.css'
import cv from "../home/cv.pdf";
import {saveAs} from 'file-saver'


export default function MobileMenu({callback, shown, menuElements}) {


  function moveTo(element) {
    document.querySelector(`#${element}-before`).scrollIntoView()
  }

  function saveCV() {
    const filename = 'cv.pdf'
    const file = new File([cv], filename, {type: "application/pdf;charset=utf-8"});
    saveAs(file,filename);
  }

  return (
    <div className="full"
         onClick={callback}
         style={{
           display: shown ? 'block' : 'none'
         }}>
      <div className="close"> &#10006; </div>
      <div className="center-content">
        <ul>
          {menuElements.map((element, index) => {
            return <li key={index} onClick={() => moveTo(element)}> {element} </li>
          })}
          <button className="cv-saver" onClick={saveCV}> скачать резюме </button>
        </ul>
      </div>
    </div>
  )
}
